@mixin size($w,$h:$w,$r:0) {
  width: $w;
  height: $h;
  border-radius: $r; }

@mixin flex($j:center,$a:center) {
  display: flex;
  justify-content: $j;
  align-items: $a; }

@mixin abpos($l:initial,$t:initial) {
  position: absolute;
  left: $l;
  top: $t; }

@mixin bord($c:#000) {
  border: solid $c; }


.Checklist {
  @include size(100%);
  @include flex;
  flex-direction: column;
  //+bord

  .ChecklistBody-Boxs {} }

